<template>
  <div>
    <table>
      <thead>
        <tr>
          <th v-for="header in headers" :key="header.text">
            {{ header.text }}
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in items" :key="index">
          <td>{{ item["start_time"] }}</td>
          <td>
            {{ item["bath_time"] }}
          </td>
          <td>
            {{ item["temp"] }}
          </td>
          <td>
            {{ item["level"] }}
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
export default {
  props: {
    items: Array,
  },
  data() {
    //start_time,  end_time,  bath_time, temp,  level, user_id, bath_id
    return {
      headers: [
        {
          text: "시작",
          value: "time",
        },
        {
          text: "목욕시간",
          value: "bath_time",
        },
        {
          text: "온도",
          value: "temp",
        },
        {
          text: "물 높이",
          value: "level",
        },
      ],
    };
  },
};
</script>
<style scoped>
thead th {
  border-bottom: 1px solid #006080;
}
td {
  text-align: center;
}
</style>