<template>
  <!--예약 모달창-->
  <div>
    <ReservePopup
      v-if="isReservePopup == true"
      :isReservePopup="isReservePopup"
      @offPopup="offPopup"
    />
    <div id="appview">
      <!--상단바-->
      <div id="topbar">
        <div id="apptitle">
          <font-awesome-icon :icon="['fasr', 'arrow-left']" />
          스마트바스
        </div>
        <div id="topbar_menu">
          <font-awesome-icon :icon="['fas', 'bell']" style="color: #ff9900" />
          <ul id="user_profile">
            <li v-for="i in userArr" :key="i">
              <img :src="require(`@/assets/img/${i.img}.png`)" />
            </li>
          </ul>
        </div>
      </div>
      <!--목욕 시작 버튼 및 현재 온도-->
      <div id="bath_container">
        <div>
          <button id="bath_button" v-on:click="bathButtonClick">
            <font-awesome-icon
              v-bind:icon="bathbuttonIcon"
              :class="bathbuttonIcon_spin"
            />
            {{ bathbuttonText }}
          </button>
          <button v-on:click="sendReserve" class="btn btn-primary">
            예약하기
          </button>
          <button v-on:click="sendReserveCancel">예약취소</button>
          <button v-on:click="getBathState">새로고침</button>
        </div>

        <div>
          <span id="curr_temp">{{ bathState.temp }}</span>
          <span id="temp_sign">°C</span>
        </div>
      </div>
      <!--욕조 이미지-->
      <div style="text-align: center">
        <img style="height: 250px" src="../src/assets/img/clean.png" />
      </div>
      <div id="update_time">{{ updateTime }}</div>
      <!--탭 뷰-->
      <div id="tab">
        <div id="tabButton">
          <button v-on:click="currentTab = 'ControllerView'">컨트롤러</button>
          <button v-on:click="currentTab = 'ReserveView'">예약</button>
          <button v-on:click="currentTab = 'HistoryView'">이력</button>
        </div>
        <component
          v-bind:is="currentTabComponent"
          :userArr="userArr"
          :bathState="bathState"
          :toast="toast"
          @inputTemp="onChagebathTemp"
          @inputLevel="onChagebathLevel"
          :historyArr="historyArr"
        ></component>
      </div>
    </div>
  </div>
</template>

<script>
import ControllerView from "@/views/ControllerView.vue";
import HistoryView from "@/views/HistoryView.vue";
import ReserveView from "@/views/ReserveView.vue";
import ReservePopup from "./components/ReservePopup.vue";
import { useToast } from "vue-toastification";

//native와 메시지 교환
//webviewBridge.registerCallbackFunc(messageListener함수, partnerToken)
export default {
  name: "HomeView",
  components: {
    ControllerView,
    HistoryView,
    ReserveView,
    ReservePopup,
  },
  mounted() {
    //setInterval(this.getBathState, 5000);
    this.getHistory();
    this.getBathState();
    this.getReserveList();
  },
  setup() {
    const toast = useToast();
    var historyArr = [];
    var reserveArr = [];

    return { toast, historyArr, reserveArr };
  },
  data() {
    return {
      //유저 정보 저장 배열(수정 필요)
      userArr: [{ img: "logo" }, { img: "cap_close" }, { img: "clean" }],
      //스케쥴러 배열(예약 삭제 시 사용)
      scheduleArr: [],
      //컴포넌트 변수
      currentTab: "ControllerView",
      bathbuttonIcon_spin: "", //빈칸 아니면 fa-spin
      isReservePopup: false,
      //목욕 온도, 수위
      bathTemp: 40,
      bathLevel: 30,

      //욕조 상태 새로고침한 시간
      updateTime: " ",

      //욕조 상태 (0 아무 동작안함, 1 목욕전청소, 2 물받기,  3 물 유지)
      orderState: 1,

      //욕조 상태 변수(axios로 값 받아오기)
      bathState: {
        bathid: 1,
        state: 0,
        temp: 0,
        level: 0,
        cap: 1,
        h_valve: 0,
        c_valve: 0,
        clean_valve: 0,
      },
    };
  },
  computed: {
    //인자나 http코드가 있으면 methods 써라
    currentTabComponent() {
      return this.currentTab;
    },
    bathbuttonText() {
      if (this.orderState == 1) {
        return "목욕 시작";
      } else if (this.orderState == 2) {
        return "예약 취소";
      } else {
        return "목욕 종료";
      }
    },
    bathbuttonIcon() {
      if (this.orderState == 1) {
        return "fa-play";
      } else if (this.orderState == 2) {
        return "fa-circle-notch";
      } else {
        return "fa-stop";
      }
    },
  },
  methods: {
    bathButtonClick() {
      if (this.orderState == 1) {
        this.isReservePopup = true;
        this.orderState = 3;
        console.log(this.bathTemp + "/" + this.bathLevel);
        //this.sendReserve();
        //this.sendBathStart();
      } else if (this.orderState == 2) {
        //예약 됨
        this.orderState = 3;
      } else {
        this.orderState = 1;
        //this.sendBathFinish();
      }
    },
    //axios 백엔드에 목욕 시작 명령 전송
    sendBathStart() {
      this.$http
        .post("control/1", {
          cap: 0,
          h_valve: 0,
          c_valve: 0,
          clean: 15,
          user_id: 1,
          temp: this.bathTemp, //슬라이더 값으로
          level: this.bathLevel, //슬라이더 값으로
        })
        .then((res) => {
          console.log(res);
          this.toast("목욕을 시작합니다.");
        })
        .catch((err) => {
          console.error(err);
          this.toast.error("오류가 발생하였습니다.\n잠시후 다시 시도해주세요.");
        });
    },
    //axios 백엔드에 예약 명령 전송
    sendReserve() {
      var today = new Date();
      var year = today.getFullYear();
      var month = ("0" + (today.getMonth() + 1)).slice(-2);
      var day = ("0" + today.getDate()).slice(-2);
      var dateString = year + "-" + month + "-" + day + "T";
      var hours = ("0" + today.getHours()).slice(-2);
      var minutes = ("0" + (today.getMinutes() + 3)).slice(-2);
      var seconds = ("0" + today.getSeconds()).slice(-2);
      var timeString = hours + ":" + minutes + ":" + seconds;

      this.$http
        .post("schedule/1", {
          user_id: "1",
          temp: this.bathTemp,
          level: this.bathLevel,
          clean_time: 15,
          bath_start: dateString + "" + timeString,
        })
        .then((res) => {
          console.log(res);
          this.toast("목욕이 예약되었습니다.");
        })
        .catch((err) => {
          console.error(err);
          this.toast.error("오류가 발생하였습니다.\n잠시후 다시 시도해주세요.");
        });
    },
    //목욕 종료 명령 보내기
    sendBathFinish() {
      this.$http
        .post("control/1", {
          cap: 0,
          h_valve: 0,
          c_valve: 0,
          clean: 0,
          user_id: 1,
          temp: 99,
          level: 0,
        })
        .then((res) => {
          console.log(res);
          this.toast("목욕을 종료합니다.");
        })
        .catch((err) => {
          console.error(err);
          this.toast.error("오류가 발생하였습니다.\n잠시후 다시 시도해주세요.");
        });
    },
    //예약 리스트 가져오기
    getReserveList() {
      this.$http
        .get("schedule/1", {}) //bathid
        .then((res) => {
          console.log(res);
          this.reserveArr = res.data;
        })
        .catch((err) => {
          console.error(err);
        });
    },
    //예약 삭제
    sendReserveCancel() {
      //api로 예약 친구로부터 예약 리스트 받아오고
      //id가 같은 친구중 가장 최신을 다시 보내서 예약 삭제
      this.$http
        .get("schedule/find/1", {}) //userid
        .then((res) => {
          console.log(res);

          this.$http
            .get("schedule/delete/", {})
            .then((res) => {
              console.log(res);
              this.toast("예약이 취소되었습니다.");
            })
            .catch((err) => {
              console.error(err);
              this.toast.error(
                "오류가 발생하였습니다.\n잠시후 다시 시도해주세요."
              );
            });
        })
        .catch((err) => {
          console.error(err);
        });
    },
    //현재 욕조 상태 가져오는 함수
    async getBathState() {
      var today = new Date();
      var year = today.getFullYear();
      var month = ("0" + (today.getMonth() + 1)).slice(-2);
      var day = ("0" + today.getDate()).slice(-2);
      var dateString = year + "-" + month + "-" + day;
      var hours = ("0" + today.getHours()).slice(-2);
      var minutes = ("0" + today.getMinutes()).slice(-2);
      var seconds = ("0" + today.getSeconds()).slice(-2);
      var timeString = hours + ":" + minutes + ":" + seconds;

      this.$http
        .get("/bath/1")
        .then((res) => {
          console.log(res.data);

          this.bathState.bathid = res.data.bathid;
          this.bathState.state = res.data.state;
          this.bathState.temp = res.data.temp;
          this.bathState.level = res.data.level;
          this.bathState.cap = res.data.cap;
          this.bathState.h_valve = res.data.h_valve;
          this.bathState.c_valve = res.data.c_valve;
          this.bathState.clean_valve = res.data.clean_valve;

          this.updateTime = dateString + " " + timeString + "에 불러옴";
        })
        .catch((err) => {
          console.error(err);
        });
    },
    //해당 유저 목욕 이력 가져오는 함수
    getHistory() {
      this.$http
        .get("/history/1")
        .then((res) => {
          console.log(res.data);
          //응답: 목욕 시작시간, 목욕 종료 시간, 목욕 진행시간, 욕조 물 온도, 욕조 물 높이, 사용자 id, 기기 id
          this.historyArr = res.data;
          console.log(this.historyArr);
        })
        .catch((err) => {
          console.error(err);
        });
    },
    //controllview에서 온도값 변경했을 시 수행하는 함수
    onChagebathTemp(val) {
      this.bathTemp = val;
      //console.log(this.bathTemp + " 온도 변경");
    },
    //controllview에서 수위값 변경했을 시 수행하는 함수
    onChagebathLevel(val) {
      this.bathLevel = val;
    },
    offPopup() {
      this.isReservePopup = false;
    },
  },
};
</script>

